button:active {
  background: #7dc1e6;
}




.footer-section {
  background: #151414;
  position: fixed;
  bottom: 0;
  display: block;
  width: 100%;
}

.copyright-area{
  background: #202020;
  padding: 10px 0;
}
.copyright-text p {
  margin: 0;
  font-size: 12px;
  color: #878787;
}

.footer-menu li {
  display: inline-block;
  margin-left: 20px;
}
.footer-menu li:hover a{
  color: #ff5e14;
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}

.connect-wallet {
  position: relative;
  width: 100%;
  max-width: 662px;
  top: 22vh;
  height: auto;
  background: rgb(0 0 0 / 83%);
  padding: 29px;
  border: 10px solid rgb(251 90 144);
  margin: 20px auto;
}


button.custom-cursor.custom-cursor--action {
    /* float: left; */
    background: rgb(204 73 117);
    font-size: 26px;
    padding: 20px;
    font-weight: bold;
    border: 1px solid rgb(112 112 112);
    margin-top: 12px;
    line-height: normal;
    /* width: 100%; */
    cursor: pointer;
    text-align: center;
    color: rgb(255 255 255);
}

.button_ button.custom-cursor.custom-cursor--action {
    /* float: left; */
    background: rgb(251 90 144);
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    border: 1px solid rgb(112 112 112);
    margin: 12px 10px 0 0!important;
    line-height: normal;
    /* width: 100%; */
    cursor: pointer;
    text-align: center;
    color: rgb(255 255 255);
}

.web3modal-modal-card {
    background: rgb(251, 90, 144)!important;
}

.web3modal-provider-container {
    background: rgb(0 0 0)!important;
}
.web3modal-provider-name {
    color: rgb(255 255 255)!important;
}

.wallet_status {
  color: rgb(255 255 255);
}

.block_calc {
position: relative;
display: block;
width: 500px;
background: rgb(255 255 255);
top: 100px;
}

.calcsDetails p {
  font-size: 19px;
  font-weight: 900;
  color: rgb(255 255 255);
  line-height: 28px;
}



.calcsDetails {
  margin-bottom: 15px;
}
.calcsDetails h1 {
  text-align: center;
  margin-bottom: 10px;
}
.number-input-container {
  width: 244px;
  display: grid;
  grid-template-columns: 53px auto 54px;
  margin: 14px auto;
  height: 53px;
}

.not-connected {
  top: 37vh;
  /* bottom: 50%; */
  position: relative;
}


button.custom-cursor.custom-cursor--action.whitelist-mint {
  background: #01d100;
}

button.custom-cursor.custom-cursor--action.disconnect {
  background: rgb(0 0 0);
  /* border: rgb(0 0 0); */
}

.counter {
  /* background: rgb(255 0 0); */
  position: fixed;
  bottom: 100px;
  display: block;
  width: 100%;
  text-align: left;
  left: 10px;
  color: rgb(255 255 255);
  font-size: 15px;
  /* font-weight: bolder; */
}

.counter p {
  color: rgb(255 255 255);
  font-weight: bolder;
}

.counter ._count {
  display: block;
  margin: 10px 13px;
}

._count {
  position: relative;
}

p.whitelist {
  top: 7px;
  left: 137px;
  color: rgb(251 90 144);
  margin: 0 10px 0 0;
}
p.public {
  top: 7px;
  left: 137px;
  color: rgb(251 90 144);
  margin: 0 10px 0 0;
}

@media screen and (max-width: 767px) {
  .connect-wallet {
    position: relative;
    width: 100%;
    max-width: 647px;
    top: 12vh;
    height: auto;
    background: rgb(0 0 0 / 83%);
    padding: 16px;
    border: 10px solid rgb(251 90 144);
    margin: 18px auto;
  }

  .counter {
    font-size: 16px;
    margin: 0 auto;
    width: 100%;
    display: fixed;
    position: fixed;
    left: 0;
    display: flex;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    bottom: 74px;
  }
  p.whitelist {
    position: initial;
        top: 21px;
        left: 137px;
        color: rgb(251 90 144);
        margin: 10px;
}
p.public {    position: initial;
     top: 21px;
     left: 137px;
     color: rgb(251 90 144);
     margin: 10px;
}

.counter ._count {
  display: block;
  text-align: center;
}
}